@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --btn-80px: Inter;
  --font-overpass: Overpass;
  --font-montserrat: Montserrat;
  --font-work-sans: "Work Sans";

  /* font sizes */
  --btn-80px-size: 2.5rem;
  --font-size-lg: 1.13rem;
  --font-size-5xl: 1.5rem;
  --font-size-11xl: 1.88rem;
  --font-size-xl-8: 1.3rem;
  --font-size-3xl-9: 1.43rem;
  --font-size-4xl-8: 1.49rem;
  --font-size-mid-9: 1.12rem;
  --font-size-base-9: 0.99rem;
  --font-size-smi-9: 0.81rem;
  --font-size-lgi-8: 1.24rem;
  --font-size-28xl-6: 2.98rem;
  --font-size-xs-9: 0.74rem;
  --font-size-xl: 1.25rem;
  --font-size-17xl: 2.25rem;
  --font-size-53xl: 4.5rem;

  /* Colors */
  --color-darkslategray-100: #3d3d3d;
  --color-darkslategray-200: rgba(61, 61, 61, 0.7);
  --color-darkslategray-300: rgba(61, 61, 61, 0.69);
  --color-darkorange: #ff8a00;
  --white: #fff;
  --color-floralwhite: #fff8f0;
  --color-whitesmoke: #ededed;
  --color-cornsilk-100: #fff7df;
  --color-turquoise: #0ad1bd;
  --color-gray-100: #7a7a7a;
  --color-gray-200: #2b292d;
  --fill-color-color-3: #1c1c1c;
  --color-black: #000;
  --color-gainsboro: #dfdfdf;
  --color-lightskyblue: #5ac8fa;

  /* Gaps */
  --gap-36xl: 3.44rem;
  --gap-101xl: 7.5rem;
  --gap-9xs: 0.25rem;
  --gap-mini: 0.94rem;
  --gap-282xl: 18.81rem;
  --gap-xl: 1.25rem;

  /* Paddings */
  --padding-152xl: 10.69rem;
  --padding-34xl: 3.31rem;
  --padding-3xs: 0.63rem;
  --padding-9xs: 0.25rem;
  --padding-102xl: 7.56rem;
  --padding-11xs-7: 0.11rem;
  --padding-11xs: 0.13rem;
  --padding-12xs: 0.06rem;

  /* border radiuses */
  --br-12xl: 31px;
  --br-81xl: 100px;
  --br-base-6: 15.6px;
  --br-6xs-3: 6.3px;
  --br-20xl-7: 39.7px;
  --br-43xl: 62px;
  --br-10xs: 3px;
  --br-12xs: 1px;
}
